@value variables: "../../global.css";
@value mobileWidth from variables;

nav {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	position: absolute;
	z-index: -1;
	width: 100%;
	padding: 1.45rem 0 0 1.5rem;
	background: white;

	font-family: 'Roboto Condensed', sans-serif;
	font-weight: normal;
	/*font-size: 1.25rem;*/

	overflow: hidden;
	height: 100%;

}

nav.open {
	min-height: 100vh;
	max-width: 535px;
	background-color: black;
	color: white;
}

nav i {
	font-size: 2.2rem !important;
}

nav h2 {
	text-transform: uppercase;
	font-size: 1.5em;
}

nav ol {
	list-style: none;
	padding: 0;
	margin: 0;
}


nav ol h3 {
	font-size: 1.25em;
	margin-bottom: .3rem;
}

nav ol h4 {
	font-weight: 300;
	font-size: 1.25em;
	margin-top: 0;
}

.nav-meta {
	font-weight: 300;
	font-size: 1.25em;
	margin-top: 1rem;
}

.nav-meta a {
	font-weight: bold;
}

.nav-contents {
	display: none;
}

.nav-contents li {
	cursor: pointer;
}

nav.open .nav-contents {
	display: block;
}

.menu-toggle {
	cursor: pointer;
	position: relative;
	left: -5px;
}

.nav-contents a {
	color: inherit;
	text-decoration: none;
}

@media (mobileWidth) {
	nav {
		padding: .3rem 0 0 .3rem;
		width: 100%;
		font-size: 0.8rem;
	}

	nav i {
		font-size: 1.19rem !important;
	}

	.menu-toggle {
		left: 9px;
		top: 2px;
	}

}




