@value variables: "../../global.css";
@value mobileWidth from variables;
@value tabletWidth from variables;

.essay {
	display: -ms-flexbox;
	display: flex;
	padding-left: 5rem;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.essay__content {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	margin: 4rem 1rem 5rem 0;
	width: 38vw;
}

.essay__body {
	line-height: 1.4em;
}

.essay__figures {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	width: calc((47vw - 200px));
	max-width: 40vw;
	margin-top: 5.5rem;
	position: relative;
	max-height: 100vh;
	box-sizing: border-box;
	padding: 0 20px;
}

.essay__figures-content {
	position: fixed;
	width: calc(47vw - 200px);
	max-width: 40vw;
}

.essay__figures figure {
	margin: 0 40px;
}

.essay__figures img {
	width: 100%;
	max-height: 75vh;
	-o-object-fit: contain;
	   object-fit: contain;
}

.essay__figures figure {
	margin-bottom: 5rem;
}

.essay__figures figcaption {
	margin-top: 1rem;
}

.essay__figure-caption {
	font-size: .9em;
	font-weight: 200;
	line-height: 1.5em;
	word-wrap: break-word;
}

.essay__figure-caption a {
	color: inherit;
	text-decoration: none;
}

.essay__figures-prev,
.essay__figures-next {
	position: absolute;
	top: 50vh;
	background: transparent;
	border: 0;
	z-index: 1;
	width: 40px;
	outline: none;
}

.essay__figures-next {
	right: 0;
}

.essay__figures-prev {
	left: 0;
}

.essay__links {
	padding: 2rem 1rem;
	position: fixed;
	z-index: 1000;
	border-left: 2px solid #ccc;
	width: 200px;
	height: 100vh;
	right: 0;
	background: white;
	overflow: hidden;
}

.essay__links ol {
	list-style: none;
	padding: 0;
}

.essay__links li {
	padding: 1rem;
	border-top: 2px solid #ccc;
	height: 14.5vh;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
}

.essay__links li a {
	display: block;
}

.essay__links h2 {
	text-align: center;
	text-transform: uppercase;
	font-size: 1em;
	margin: 0;
}

.essay__links h3 {
	text-align: center;
	font-size: 13px;
	margin-bottom: 0;
}

.essay__links img {
	width: 100%;
	max-height: 10vh;
	-o-object-fit: cover;
	   object-fit: cover;
}

.essay__link {
	color: inherit;
	text-decoration: none;
}

@media (tabletWidth) {

/*}*/

/*@media (mobileWidth) {*/
	.essay__figures {
		display: none;
	}

	.essay {
		padding: 0 1.5rem;
		width: 100%;
		-ms-flex-direction: column;
		    flex-direction: column;
	}

	.essay__content {
		width: 100%;
	}

	.essay__links {
		height: auto;
		position: static;
		width: 100%;
		border: none;
		-webkit-box-sizing: border-box;
		        box-sizing: border-box;
		padding-top: 0;
	}

	.essay__links li {
		height: auto;
		padding: .2rem;
		border: none;
	}

	.essay__link img {
		display: none;
	}

	.essay__links h3 {
		text-align: left;
		margin: 0;
	}

	.essay__links h2 {
		border-bottom: 2px solid #ccc;
		padding-bottom: 1rem;
	}
}


