@value variables: "../../global.css";
@value mobileWidth from variables;

.image-detail {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: rgba(0, 0, 0, .8);
	background-size: contain;
	background-position: 50%;
	background-repeat: no-repeat;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: end;
	    justify-content: flex-end;
	-ms-flex-direction: column;
	    flex-direction: column;
	z-index: 1000;
	font-size: 1rem;
}

.image-detail h2 {
	font-size: .9em;
	width: 40%;
	max-width: 600px;
	color: white;
	background-color: rgba(0, 0, 0, .8);
	padding: 20px 20px 0 20px;
	margin: 0;
}

.image-detail_caption {
	font-size: 14px;
	font-weight: 200;
	line-height: 1.3em;
	width: 40%;
	max-width: 600px;
	color: white;
	background-color: rgba(0, 0, 0, .8);
	padding: 0 20px 20px 20px;
	margin: 0 0 2% 0;
	word-wrap: break-word;
}

.image-detail_caption a {
	color: inherit;
	text-decoration: none;
}

.image-detail-close {
	position: fixed;
	top: 10px;
	right: 10px;
	width: 20px;
	cursor: pointer;
}

.image-detail-close img {
	width: 100%;
}

@media (mobileWidth) {
	.image-detail {
		-ms-flex-pack: end;
		    justify-content: flex-end;
		background-position: top;
	}

	.image-detail h2,
	.image-detail_caption {
		width: 100%;
		padding: 1rem;
		-webkit-box-sizing: border-box;
		        box-sizing: border-box;
	}


}


