@value variables: "../../global.css";
@value mobileWidth from variables;

header {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 10;
	padding: 0 0 1.2rem 0;
	width: 100%;
	background-color: white;
}

nav.open + h1 {
	color: white;
}

header h1 {
	text-transform: uppercase;
	margin: 1.5rem 0 0 5rem;
	padding: 0;
	font-weight: normal;
	font-size: 1.75rem;
}

header a {
	color: inherit;
	text-decoration: none;
}

@media (mobileWidth) {
	header {
		padding: 0 0rem .5rem 0;
	}

	header h1 {
		font-size: 0.93rem;
		margin: .5rem 1.5rem 0 1.5rem;
		text-align: center;
	}
}




