@value variables: "../../global.css";
@value mobileWidth from variables;

.home {
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	display: -ms-flexbox;
	display: flex;
	overflow: hidden;
}

.timeline-item {
	padding: 10vw 5vw;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	width: 30vw;
}

.timeline-item.down {
	-ms-flex-pack: end;
	    justify-content: flex-end;
}

.timeline-item.center {
	-ms-flex-pack: distribute;
	    justify-content: space-around;
}

.timeline-item.two-up {
	-ms-flex-pack: distribute;
	    justify-content: space-around;
}

.timeline-item.two-up .timeline-item-image {
	height: 45%;
}

.timeline-item.two-up .timeline-item-image img {
	width: auto;
	max-width: 100%;
	max-height: 100%;
}

.timeline-item-image {
	width: 100%;
}

.timeline-item-image.left {
	text-align: left;
}

.timeline-item-image.left img {
	padding-right: 50px;
}

.timeline-item-image.right {
	text-align: right;
}

.timeline-item-image.right img {
	padding-left: 50px;
}

.timeline-item-image img {
	width: 100%;
}

@media (mobileWidth) {
	.home {
		-ms-flex-direction: column;
		    flex-direction: column;
		width: 100%;
		padding-top: 2rem;
		overflow: auto;
		position: static;
	}

	.timeline-item {
		display: block;
		width: 100% !important;
		padding: 0;
	}

	.timeline-item-image.left img,
	.timeline-item-image.right img {
		padding-left: 0;
		padding-right: 0;
	}

	.timeline-item-image {
		padding: 10vw 0;
	}

}




